import { graphql, Link } from 'gatsby';
import React from 'react';
import Media from 'react-media';

// Components
import '../../../node_modules/react-image-gallery/styles/css/image-gallery.css';
import Layout from '../Layout/Layout';
import Breadcrumbs from '../Breadcrumbs/Breadcrumbs';
import ProductBuy from '../ProductBuy/ProductBuy';
import ProductPrice from '../ProductPrice/ProductPrice';
import ImageGallery from 'react-image-gallery';
import ProductWarranty from '../ProductWarranty/ProductWarranty';

export const query = graphql`
	query($title: String!) {
		contentfulProduct(title: { content: { elemMatch: { content: { elemMatch: { value: { eq: $title } } } } } }) {
			title {
				content {
					content {
						value
					}
				}
			}
			desc {
				content {
					content {
						value
					}
				}
			}
			link {
				content {
					content {
						value
					}
				}
			}
			img {
				file {
					url
				}
			}
			category
			price
		}
	}
`;

function Product({ data }) {
	const title = data.contentfulProduct.title.content[0].content[0].value;
	const desc = data.contentfulProduct.desc.content[0].content[0].value;
	const link = data.contentfulProduct.link.content[0].content[0].value;
	const price = data.contentfulProduct.price;
	const lavazem = 'لوازم جانبی';
	const consol = 'کنسول بازی';
	const category = data.contentfulProduct.category;
	const cat = category === 'console' ? consol : lavazem;

	const returnImages = () => {
		const imgArray = [];
		data.contentfulProduct.img.forEach((each) => {
			imgArray.push({ original: each.file.url, thumbnail: each.file.url });
		});
		return imgArray;
	};
	const dataImages = returnImages();

	return (
		<Layout title={title}>
			<Breadcrumbs textone="فروشگاه" linkone="/shop" texttwo={cat} linktwo={`/category/${category}`} />
			<div className="container mt-1 pb-3" style={{ background: 'white', borderRadius: '7px' }}>
				<div className="row ">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 text-right mt-3">
						<h3 dir="rtl">{title}</h3>
						<hr className="m-0 p-0" style={{ background: '#dadada' }} />

						<div className="d-flex justify-content-end align-items-center">
							<Link style={{ paddingTop: '3px' }} className="pr-1" to={`/category/${category}`}>
								{cat}
							</Link>
							<p style={{ color: '#9e9e9e' }} className="pt-3">
								: دسته بندی
							</p>
						</div>

						<div className="text-right mt-1">
							<p dir="rtl">{desc}</p>
						</div>

						<div dir="rtl" className="d-flex justify-content-between align-items-center pb-3 pt-4">
							<ProductWarranty />
							<ProductPrice price={price} />
						</div>
						<Media query={`(min-width: 960px)`} render={() => <ProductBuy link={link} />} />
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 mt-3">
						<ImageGallery lazyLoad={true} showPlayButton={false} items={dataImages} />
						<Media query={`(max-width: 960px)`} render={() => <ProductBuy link={link} />} />
					</div>
				</div>
			</div>
		</Layout>
	);
}

export default Product;
