import React from 'react';
import classes from './ProductWarranty.module.scss';
import check from '../../img/check.png';

function ProductWarranty() {
	return (
		<div className="d-flex align-items-center">
			<img alt="check" src={check} style={{ width: '21px' }} />
			<p className={`m-0 p-0 mr-2 ${classes.text}`}>گارانتی اصالت و سلامت فیزیکی کالا</p>
		</div>
	);
}

export default ProductWarranty;
