import React from 'react';
import { Link } from 'gatsby';
import left from '../../img/left.png';
import classes from './Breadcrumbs.module.scss';

function Breadcrumbs({ textone, texttwo, linkone, linktwo }) {
	return (
		<div className="container mt-2">
			<div dir="rtl" className="d-flex justify-content-start align-items-center">
				<Link className={`p-2 ${classes.links}`} to="/">
					صفحه اصلی
				</Link>
				<img alt="arrow" src={left} className={classes.arrow} />
				<Link className={`p-2 ${classes.links}`} to={linkone}>
					{textone}
				</Link>
				<img alt="arrow" src={left} className={classes.arrow} />
				<Link className={`p-2 ${classes.links}`} to={linktwo}>
					{texttwo}
				</Link>
			</div>
		</div>
	);
}

export default Breadcrumbs;
